@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: 'Noto Sans JP', sans-serif;
  font-size: 62.5%;
}

body {
  margin: 0;
  background-color: #3f3f3f;
}


.expense-filter{
    padding: 0 0.8rem;
    margin: 1.2rem 0 1.6rem 0;
    text-align: right;
}

.expenseFilter-controls{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.expenseFilter-controls label {
    color: #fff;
    font-size: 1.4rem;
    font-weight: 600;
}

.expenseFilter-controls select {
    padding: 0.8rem 2.4rem;
    border-radius: 9px;
    font-weight: 600;
}

.year-select{
    cursor: pointer;
}
.Expense-list{
    list-style: none;
    padding: 0 0.8rem;
}

.no-expense {
    color: #fff;
    text-align: center;
    font-size: 1.8rem;
    padding: 1.4rem;
}
.expense-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  grid-gap: 1.2rem;
  gap: 1.2rem;
  padding: 0.8rem;
  background-color: #3f3f3f;
  margin: 1rem 0;
}

.expense-item-description {
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: #fff;
  flex: 1 1;
}

.expense-item-description h2 {
  color: #fff;
  font-size: 1.6rem;
}

.expense-item-price {
  font-size: 1.6rem;
  font-weight: 700;
  padding: 0.8rem 2rem;
  border-radius: 1.5rem;
  border: 1px solid #fff;
  color: #fff;
  background-color: rgb(47, 29, 62);
}

.expense-calender {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 6.2rem;
    text-align: center;
    padding: 0.4rem 0;
    background-color: #222;
    color: #fff;
    border: 1px solid #fff;
    border-radius: 1rem;
  }
  
  .month {
    font-size: 1rem;
    font-weight: bold;
  }
  .year {
    font-size:1rem;
  }
  .day {
    margin-top: 0.2rem;
    font-size: 1.6rem;
    font-weight: bold;
  }
  
.card {
    border-radius: 1rem;
    box-shadow: 0 1rem 1.2rem #333;
}
.chart-bar{
    display: flex;
    padding: 0.8rem 0 0.8rem 0;
    height: 10rem;
    flex-direction: column;
}

.chart-bar__fill__box{
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    border-radius: 8px;
    width: 1rem;
    background-color:rgba(155, 97, 186, 0.432);
    border: 1.5px solid rgb(112, 67, 137);
    overflow: hidden;
    transition: all 0.3s ease-in;
}

.chart-bar__fill{
    background-color: rgb(65, 65, 209);
    width: 100%;
    border-radius: 8px;
    transition: all 0.3s;
    
}

.chart-bar__label{
    font-size: 0.6rem;
    font-weight: bold;
    color: #222;
    text-align: center;
}
.Chart {
    display: flex;
    justify-content: space-evenly;
    border-radius: 8px;
    padding: 0 1.6rem;
    margin: 0 0.8rem;
    background-color:rgb(228, 209, 246);
}



.expenses {
    background-color: #222;
    margin: 2rem auto;
    width: 64rem;
    padding: 1.2rem 0.8rem;
}


.new-expenses-form {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 1.2rem;
  gap: 1.2rem;
  width: 80%;
  margin-bottom: 1.6rem;
}

.new-expense-controls {
  display: flex;
  text-align: left;
  flex-direction: column;
  grid-gap: 0.4rem;
  gap: 0.4rem;
}

.new-expense-controls label {
  color: rgb(47, 29, 62);
  font-size: 1.2rem;
  font-weight: 600;
}

.new-expense-controls input {
  border-radius: 4px;
  border: none;
  height: 2.8rem;
  padding: 0 0.4rem;
}

.new-expenses-btn {
  display: flex;
  grid-gap: 1rem;
  gap: 1rem;
  justify-content: flex-end;
  margin-right: 1rem;
}

.btn-form,
.btn-form:visited {
  padding: 1.4rem 2.4rem;
  font-size: 1.2rem;
  border: none;
  background-color: rgb(47, 29, 62);
  color: #fff;
  border-radius: 9px;
}
.btn-form:hover,
.btn-form:active {
  cursor: pointer;
  background-color: rgba(47, 29, 62, 0.91);
}

.newExpense {
  width: 64rem;
  margin: 2rem auto 0 auto;
  padding: 1rem;
  text-align: center;
  background-color: rgba(185, 147, 255, 0.721);
}

.newExpense button,
.newExpense button:visited {
  padding: 1.4rem 2.4rem;
  font-size: 1.2rem;
  border: none;
  background-color: rgb(47, 29, 62);
  color: #fff;
  border-radius: 9px;
}

.newExpense button:hover,
.newExpense button:active {
  cursor: pointer;
  background-color: rgba(47, 29, 62, 0.91);
}

